/**
 * Attack Order Optimization Types
 * Defines interfaces and types for intelligent attack order optimization
 * supporting both player and enemy unit optimization
 */

import { StackingGroup } from './units';
import { EnemyUnit } from './enemyUnits';

/**
 * Represents any unit that can participate in combat
 */
export type CombatUnit = StackingGroup | EnemyUnit;

/**
 * Identifies the side a unit belongs to
 */
export type UnitSide = 'player' | 'enemy';

/**
 * Detailed damage calculation between an attacker and target
 */
export interface DamageCalculation {
  /** The attacking unit */
  attacker: CombatUnit;
  /** The target unit being attacked */
  target: CombatUnit;
  /** Base damage before modifiers */
  baseDamage: number;
  /** Final effective damage after all modifiers */
  effectiveDamage: number;
  /** List of modifiers applied to this calculation */
  modifiersApplied: Array<{
    /** Type of modifier (e.g., "vs Melee", "vs Flying") */
    type: string;
    /** Modifier value (positive = bonus, negative = penalty) */
    value: number;
    /** Source of the modifier */
    source: string;
  }>;
  /** Which side this attacker belongs to */
  attackerSide: UnitSide;
  /** Which side this target belongs to */
  targetSide: UnitSide;
}

/**
 * Matrix of damage calculations for all attacker/target combinations
 */
export interface DamageMatrix {
  /** All damage calculations organized by attacker index then target index */
  calculations: DamageCalculation[][];
  /** List of all attacking units in order */
  attackers: CombatUnit[];
  /** List of all target units in order */
  targets: CombatUnit[];
  /** Total time taken to calculate the matrix in milliseconds */
  calculationTimeMs: number;
}

/**
 * Result of attack order optimization for one side
 */
export interface AttackOrderOptimization {
  /** Original attack order before optimization */
  originalOrder: CombatUnit[];
  /** Optimized attack order for maximum damage */
  optimizedOrder: CombatUnit[];
  /** Damage matrix used for optimization */
  damageMatrix: DamageMatrix;
  /** Total damage with original order */
  originalTotalDamage: number;
  /** Total damage with optimized order */
  optimizedTotalDamage: number;
  /** Percentage improvement from optimization */
  improvementPercentage: number;
  /** Which side this optimization applies to */
  side: UnitSide;
  /** Time taken for optimization in milliseconds */
  optimizationTimeMs: number;
}

/**
 * Complete bidirectional optimization result
 */
export interface BidirectionalOptimization {
  /** Player side optimization */
  playerOptimization: AttackOrderOptimization;
  /** Enemy side optimization */
  enemyOptimization: AttackOrderOptimization;
  /** Combined improvement metrics */
  combinedMetrics: {
    /** Total damage improvement for both sides */
    totalDamageImprovement: number;
    /** Average improvement percentage */
    averageImprovementPercentage: number;
    /** Total optimization time */
    totalOptimizationTimeMs: number;
  };
}

/**
 * Configuration options for attack order optimization
 */
export interface OptimizationConfig {
  /** Whether to optimize player attack order */
  optimizePlayerOrder: boolean;
  /** Whether to optimize enemy attack order */
  optimizeEnemyOrder: boolean;
  /** Whether to apply unit-vs-unit modifiers */
  useUnitModifiers: boolean;
  /** Maximum calculation time before timeout (ms) */
  maxCalculationTimeMs: number;
  /** Whether to use caching for repeated calculations */
  enableCaching: boolean;
  /** Algorithm to use for optimization */
  algorithm: 'greedy' | 'dynamic' | 'heuristic';
}

/**
 * Unit type mapping for modifier calculations
 */
export interface UnitTypeMapping {
  /** Unit name to unit types mapping */
  unitTypes: Record<string, string[]>;
  /** Reverse mapping: type to units that have it */
  typeToUnits: Record<string, string[]>;
  /** Confidence score for each mapping (0-1) */
  confidence: Record<string, number>;
}

/**
 * Modifier application result
 */
export interface ModifierApplication {
  /** Base value before modifier */
  baseValue: number;
  /** Final value after modifier */
  modifiedValue: number;
  /** List of modifiers that were applied */
  appliedModifiers: Array<{
    type: string;
    value: number;
    source: string;
  }>;
  /** Whether any modifiers were applied */
  hasModifiers: boolean;
}

/**
 * Performance metrics for optimization
 */
export interface OptimizationMetrics {
  /** Total units processed */
  totalUnitsProcessed: number;
  /** Total damage calculations performed */
  totalCalculations: number;
  /** Cache hit rate (0-1) */
  cacheHitRate: number;
  /** Memory usage in bytes */
  memoryUsageBytes: number;
  /** Calculation time breakdown */
  timeBreakdown: {
    damageCalculation: number;
    matrixGeneration: number;
    optimization: number;
    caching: number;
  };
}

/**
 * Utility functions for working with combat units
 */
export class CombatUnitUtils {
  /**
   * Check if a combat unit is a player unit (StackingGroup)
   */
  static isPlayerUnit(unit: CombatUnit): unit is StackingGroup {
    return 'unitName' in unit && 'count' in unit;
  }

  /**
   * Check if a combat unit is an enemy unit
   */
  static isEnemyUnit(unit: CombatUnit): unit is EnemyUnit {
    return 'name' in unit && 'unit_types' in unit && !('count' in unit);
  }

  /**
   * Get the name of a combat unit
   */
  static getUnitName(unit: CombatUnit): string {
    if (CombatUnitUtils.isPlayerUnit(unit)) {
      return unit.unitName;
    } else {
      return unit.name;
    }
  }

  /**
   * Get the unit types of a combat unit
   */
  static getUnitTypes(unit: CombatUnit, typeMapping?: UnitTypeMapping): string[] {
    if (CombatUnitUtils.isEnemyUnit(unit)) {
      return unit.unit_types;
    } else if (typeMapping) {
      return typeMapping.unitTypes[unit.unitName] || [];
    } else {
      return [];
    }
  }

  /**
   * Get the base strength of a combat unit
   */
  static getBaseStrength(unit: CombatUnit): number {
    if (CombatUnitUtils.isPlayerUnit(unit)) {
      return unit.unitStrength;
    } else {
      return unit.strength;
    }
  }

  /**
   * Get the health of a combat unit
   */
  static getHealth(unit: CombatUnit): number {
    if (CombatUnitUtils.isPlayerUnit(unit)) {
      return unit.totalHealth;
    } else {
      return unit.health;
    }
  }

  /**
   * Calculate total health for a combat unit (considering count for player units)
   */
  static calculateTotalHealth(unit: CombatUnit): number {
    if (CombatUnitUtils.isPlayerUnit(unit)) {
      return unit.totalHealth * unit.count;
    } else {
      return unit.health;
    }
  }

  /**
   * Calculate base damage for a combat unit
   */
  static calculateBaseDamage(unit: CombatUnit): number {
    if (CombatUnitUtils.isPlayerUnit(unit)) {
      return unit.count * unit.unitStrength;
    } else {
      return unit.strength;
    }
  }

  /**
   * Format a combat unit for display
   */
  static formatForDisplay(unit: CombatUnit): string {
    if (CombatUnitUtils.isPlayerUnit(unit)) {
      return `${unit.count} ${unit.unitName}`;
    } else {
      return unit.name;
    }
  }
}

/**
 * Default optimization configuration
 */
export const DEFAULT_OPTIMIZATION_CONFIG: OptimizationConfig = {
  optimizePlayerOrder: true,
  optimizeEnemyOrder: true,
  useUnitModifiers: true,
  maxCalculationTimeMs: 5000,
  enableCaching: true,
  algorithm: 'greedy'
};

/**
 * Constants for attack optimization
 */
export const ATTACK_OPTIMIZATION_CONSTANTS = {
  /** Maximum number of units to optimize before using heuristics */
  MAX_UNITS_FOR_EXACT_OPTIMIZATION: 50,
  /** Cache size limit for damage calculations */
  MAX_CACHE_SIZE: 10000,
  /** Default timeout for optimization calculations */
  DEFAULT_TIMEOUT_MS: 5000,
  /** Minimum improvement percentage to consider optimization worthwhile */
  MIN_IMPROVEMENT_THRESHOLD: 0.1
} as const;