/**
 * Enemy behavior abstractions for handling mortal vs immortal enemies
 * Following the Strategy pattern to encapsulate different enemy behaviors
 */

import { Unit } from './units';
import { CombatAction } from './battleSimulation';

/**
 * Result of applying damage to an enemy unit
 */
export interface EnemyDamageResult {
  /** Whether the enemy unit/group was eliminated */
  eliminated: boolean;
  /** Actual damage dealt (may be less than requested if enemy dies) */
  actualDamage: number;
  /** Remaining health after damage */
  remainingHealth: number;
  /** Remaining count after damage (undefined for immortal enemies) */
  remainingCount?: number;
  /** Whether this enemy unit is completely defeated */
  isDefeated: boolean;
}

/**
 * Abstract interface for enemy behavior strategies
 * Defines how different types of enemies respond to damage and attacks
 */
export interface IEnemyBehavior {
  /**
   * Apply damage to an enemy unit
   * @param enemy The enemy unit receiving damage
   * @param damage Amount of damage to apply
   * @returns Result of damage application
   */
  applyDamage(enemy: Unit, damage: number): EnemyDamageResult;

  /**
   * Check if enemy can still attack (has remaining units/health)
   * @param enemy The enemy unit to check
   * @returns Whether this enemy can still participate in battle
   */
  canAttack(enemy: Unit): boolean;

  /**
   * Get effective strength for attack (may be modified by remaining count)
   * @param enemy The attacking enemy unit
   * @returns Effective attack strength
   */
  getAttackStrength(enemy: Unit): number;

  /**
   * Create combat action when this enemy attacks
   * @param enemy The attacking enemy unit
   * @param targetGroup The player group being attacked
   * @param turn Current battle turn
   * @returns Combat action for the attack
   */
  createAttackAction(enemy: Unit, targetGroup: any, turn: number): CombatAction;

  /**
   * Create combat action when this enemy takes damage
   * @param enemy The enemy unit taking damage
   * @param attacker The attacking player group
   * @param damage Amount of damage dealt
   * @param turn Current battle turn
   * @returns Combat action for taking damage
   */
  createDamageAction(enemy: Unit, attacker: any, damage: number, turn: number): CombatAction;

  /**
   * Get display name for combat log
   * @param enemy The enemy unit
   * @returns Display name including count information if applicable
   */
  getDisplayName(enemy: Unit): string;
}

/**
 * Behavior for mortal enemies with specific counts that can be killed
 */
export class MortalEnemyBehavior implements IEnemyBehavior {
  
  applyDamage(enemy: Unit, damage: number): EnemyDamageResult {
    if (!enemy.count || enemy.count <= 0) {
      return {
        eliminated: false,
        actualDamage: 0,
        remainingHealth: 0,
        remainingCount: 0,
        isDefeated: true
      };
    }

    const currentHealth = enemy.currentHealth ?? (enemy.count * enemy.health);
    const actualDamage = Math.min(damage, currentHealth);
    const remainingHealth = Math.max(0, currentHealth - actualDamage);
    
    // Calculate how many units were killed
    const unitsKilled = Math.floor(actualDamage / enemy.health);
    const remainingCount = Math.max(0, enemy.count - unitsKilled);
    
    // Update enemy state
    enemy.currentHealth = remainingHealth;
    enemy.count = remainingCount;
    
    const isDefeated = remainingCount === 0 || remainingHealth === 0;
    
    return {
      eliminated: unitsKilled > 0,
      actualDamage,
      remainingHealth,
      remainingCount,
      isDefeated
    };
  }

  canAttack(enemy: Unit): boolean {
    return (enemy.count ?? 0) > 0 && (enemy.currentHealth ?? enemy.health) > 0;
  }

  getAttackStrength(enemy: Unit): number {
    // For mortal enemies, strength scales with remaining units
    const remainingCount = enemy.count ?? 1;
    return remainingCount > 0 ? enemy.strength : 0;
  }

  createAttackAction(enemy: Unit, targetGroup: any, turn: number): CombatAction {
    return {
      turn,
      attacker: this.getDisplayName(enemy),
      target: `${targetGroup.count} ${targetGroup.unitName}`,
      action: `attack and kill ${targetGroup.count} ${targetGroup.unitName}`,
      eliminated: true
    };
  }

  createDamageAction(enemy: Unit, attacker: any, damage: number, turn: number): CombatAction {
    const unitsKilled = Math.floor(damage / enemy.health);
    const actionText = unitsKilled > 0 
      ? `attack ${this.getDisplayName(enemy)} and kill ${unitsKilled} units (${damage.toLocaleString()} damage)`
      : `attack ${this.getDisplayName(enemy)} and deal ${damage.toLocaleString()} damage`;

    return {
      turn,
      attacker: `${attacker.count} ${attacker.unitName}`,
      target: this.getDisplayName(enemy),
      action: actionText,
      damageDealt: damage,
      eliminated: false
    };
  }

  getDisplayName(enemy: Unit): string {
    const count = enemy.count ?? 0;
    return count > 0 ? `${count} ${enemy.name}` : enemy.name;
  }
}

/**
 * Behavior for immortal enemies with infinite count
 */
export class ImmortalEnemyBehavior implements IEnemyBehavior {
  
  applyDamage(enemy: Unit, damage: number): EnemyDamageResult {
    // Immortal enemies never die, but we track damage for statistics
    return {
      eliminated: false,
      actualDamage: damage,
      remainingHealth: enemy.health, // Always full health
      remainingCount: undefined, // Infinite
      isDefeated: false
    };
  }

  canAttack(enemy: Unit): boolean {
    // Immortal enemies can always attack
    return true;
  }

  getAttackStrength(enemy: Unit): number {
    // Immortal enemies always have full strength
    return enemy.strength;
  }

  createAttackAction(enemy: Unit, targetGroup: any, turn: number): CombatAction {
    return {
      turn,
      attacker: this.getDisplayName(enemy),
      target: `${targetGroup.count} ${targetGroup.unitName}`,
      action: `attack and kill ${targetGroup.count} ${targetGroup.unitName}`,
      eliminated: true
    };
  }

  createDamageAction(enemy: Unit, attacker: any, damage: number, turn: number): CombatAction {
    return {
      turn,
      attacker: `${attacker.count} ${attacker.unitName}`,
      target: this.getDisplayName(enemy),
      action: `attack ${this.getDisplayName(enemy)} and deal ${damage.toLocaleString()} damage`,
      damageDealt: damage,
      eliminated: false
    };
  }

  getDisplayName(enemy: Unit): string {
    return enemy.name; // No count for immortal enemies
  }
}

/**
 * Factory for creating appropriate enemy behavior based on enemy type
 */
export class EnemyBehaviorFactory {
  private static mortalBehavior = new MortalEnemyBehavior();
  private static immortalBehavior = new ImmortalEnemyBehavior();

  /**
   * Get the appropriate behavior strategy for an enemy unit
   */
  static getBehavior(enemy: Unit): IEnemyBehavior {
    // Check if enemy has a specific count (mortal) or is infinite (immortal)
    if (enemy.isMortal === true || enemy.count !== undefined) {
      return this.mortalBehavior;
    } else {
      return this.immortalBehavior;
    }
  }

  /**
   * Initialize enemy unit for battle (set up tracking properties)
   */
  static initializeEnemyForBattle(enemy: Unit): Unit {
    const battleEnemy = { ...enemy };
    
    if (enemy.count !== undefined) {
      // Mortal enemy with specific count
      battleEnemy.isMortal = true;
      battleEnemy.currentHealth = enemy.count * enemy.health;
    } else {
      // Immortal enemy
      battleEnemy.isMortal = false;
      battleEnemy.count = undefined;
      battleEnemy.currentHealth = enemy.health;
    }
    
    return battleEnemy;
  }
}