/**
 * Unit types and interfaces for the TotalBattle Army Calculator
 * Unified interface supporting both player and enemy units
 */

export type CostType = 'Leadership' | 'Dominance' | 'Authority' | 'Mercenary';
export type UnitContext = 'player' | 'enemy' | 'simulation';

/**
 * Attack modifier interface for unit-vs-unit combat bonuses
 */
export interface AttackModifier {
  /** Type of unit this modifier targets (e.g., 'Melee', 'Flying', 'Mounted') */
  target_type: string;
  /** Type of modifier (typically 'Strength') */
  modifier_type: string;
  /** Absolute value of the modifier */
  value: number;
}

/**
 * Unified unit interface representing any TotalBattle unit
 * Supports both player units (with costs) and enemy units (without costs)
 */
export interface Unit {
  /** Display name of the unit */
  name: string;
  /** Array of unit type tags (e.g., ['Human', 'Ranged', 'Guardsman']) */
  unit_types: string[];
  /** Unit health points */
  health: number;
  /** Unit attack strength */
  strength: number;
  /** Attack modifiers for unit-vs-unit combat bonuses */
  attack_modifiers?: AttackModifier[];
  
  // Player-specific fields (optional for enemy units)
  /** Primary cost type for this unit (player units only) */
  cost_type?: CostType;
  /** Leadership cost to deploy this unit (player units only) */
  leadership_cost?: number;
  /** Dominance cost to deploy this unit (player units only) */
  dominance_cost?: number;
  /** Authority cost to deploy this unit (for mercenaries, player units only) */
  authority_cost?: number;
  /** Food consumption per unit (player units only) */
  food_consumption?: number;
  /** Carrying capacity of the unit (player units only) */
  carrying_capacity?: number;
  /** Gold cost to revive this unit (player units only) */
  revival_cost_gold?: number;
  /** Silver cost to revive this unit (player units only) */
  revival_cost_silver?: number;
  /** Source screenshot file this unit was extracted from (player units only) */
  source_file?: string;

  // Enemy-specific battle properties
  /** Current count of this enemy unit (for mortal enemies, undefined = infinite/immortal) */
  count?: number;
  /** Current health remaining for this enemy unit/group (for damage tracking) */
  currentHealth?: number;
  /** Whether this enemy is mortal (can be killed) or immortal (infinite) */
  isMortal?: boolean;

  // Context and metadata
  /** Usage context to distinguish between player, enemy, or simulation units */
  context?: UnitContext;
  /** Unique identifier (user-created units) */
  id?: string;
  /** Timestamp when the unit was created (user-created units) */
  createdAt?: Date;
  /** Timestamp when the unit was last modified (user-created units) */
  modifiedAt?: Date;

  // Legacy properties for backward compatibility
  class?: string;
  leadershipCost?: number;
  dominanceCost?: number;
  description?: string;
}

/**
 * Unit with calculated properties for optimization
 */
export interface UnitWithCalculatedProps extends Unit {
  readonly isMercenary: boolean;
  readonly primaryCost: number;
  readonly strengthPerCost: number;
  readonly healthPerCost: number;
}

/**
 * A group of identical units in the stacking order
 */
export interface StackingGroup {
  /** Name of the unit */
  unitName: string;
  /** Number of units in this group */
  count: number;
  /** Total health of all units in this group */
  totalHealth: number;
  /** Individual unit strength */
  unitStrength: number;
}

/**
 * Complete army composition with stacking analysis
 */
export interface ArmyComposition {
  /** Map of unit names to quantities */
  units: Record<string, number>;
  /** Total army strength */
  totalStrength: number;
  /** Total army health */
  totalHealth: number;
  /** Total leadership cost */
  totalLeadershipCost: number;
  /** Total dominance cost */
  totalDominanceCost: number;
  /** Total mercenary count */
  totalMercenaryCount: number;
  /** Stacking order from weakest to strongest */
  stackingOrder: StackingGroup[];
  /** Whether the stacking is valid (weaker units have higher total health) */
  isValidStacking: boolean;
  /** Efficiency score (strength per cost with stacking bonus) */
  efficiencyScore: number;
  /** Legacy properties for backward compatibility */
  effectiveness?: number;
  totalLeadership?: number;
  totalDominance?: number;
  metadata?: any;
}

/**
 * Constraints for army optimization
 */
export interface OptimizationConstraints {
  /** Total leadership points available */
  leadershipBudget: number;
  /** Total dominance points available */
  dominanceBudget: number;
  /** Map of mercenary names to maximum quantities */
  mercenaryLimits: Record<string, number>;
  /** Array of unit names that are available for selection */
  availableUnits: string[];
  /** Legacy budget property for backward compatibility */
  budget?: { leadership: number; dominance: number };
  /** Legacy modifiers property for backward compatibility */
  modifiers?: ClassModifiers;
}

/**
 * Result of army optimization
 */
export interface OptimizationResult {
  /** Array of valid army compositions, sorted by efficiency */
  compositions: ArmyComposition[];
  /** Total number of combinations evaluated */
  totalCombinationsEvaluated: number;
  /** Number of valid stacking solutions found */
  validStackingsFound: number;
  /** Time taken to complete optimization in milliseconds */
  executionTimeMs: number;
}

/**
 * Filter criteria for units
 */
export interface UnitFilter {
  /** Filter by cost type */
  costType?: CostType;
  /** Filter by unit types (must have at least one) */
  unitTypes?: string[];
  /** Minimum strength requirement */
  minStrength?: number;
  /** Maximum cost requirement */
  maxCost?: number;
}

/**
 * Summary statistics for loaded units
 */
export interface UnitSummary {
  /** Total number of units */
  totalUnits: number;
  /** Count of units by cost type */
  byCostType: Record<CostType, number>;
  /** Strength range across all units */
  strengthRange: {
    min: number;
    max: number;
  };
  /** Health range across all units */
  healthRange: {
    min: number;
    max: number;
  };
}

/**
 * Unified utility class for unit operations (both player and enemy units)
 */
export class UnitUtils {
  /**
   * Check if unit is a player unit (has cost information)
   */
  static isPlayerUnit(unit: Unit): boolean {
    return unit.cost_type !== undefined || unit.context === 'player';
  }

  /**
   * Check if unit is an enemy unit (no cost information)
   */
  static isEnemyUnit(unit: Unit): boolean {
    return unit.cost_type === undefined || unit.context === 'enemy';
  }

  /**
   * Check if enemy unit is mortal (can be killed)
   */
  static isMortalEnemy(unit: Unit): boolean {
    return UnitUtils.isEnemyUnit(unit) && (unit.isMortal === true || unit.count !== undefined);
  }

  /**
   * Check if enemy unit is immortal (infinite)
   */
  static isImmortalEnemy(unit: Unit): boolean {
    return UnitUtils.isEnemyUnit(unit) && !UnitUtils.isMortalEnemy(unit);
  }

  /**
   * Get remaining enemy count (undefined for immortal enemies)
   */
  static getRemainingEnemyCount(unit: Unit): number | undefined {
    return UnitUtils.isMortalEnemy(unit) ? unit.count : undefined;
  }

  /**
   * Get current enemy health (total for the group)
   */
  static getCurrentEnemyHealth(unit: Unit): number {
    if (unit.currentHealth !== undefined) {
      return unit.currentHealth;
    }
    // Calculate total health for the group
    const count = unit.count ?? 1;
    return count * unit.health;
  }

  /**
   * Check if unit is a mercenary (player units only)
   */
  static isMercenary(unit: Unit): boolean {
    return unit.cost_type === 'Mercenary' || (unit.authority_cost ?? 0) > 0;
  }

  /**
   * Get primary cost for player units (returns 0 for enemy units)
   */
  static getPrimaryCost(unit: Unit): number {
    if (!UnitUtils.isPlayerUnit(unit)) return 0;
    
    switch (unit.cost_type) {
      case 'Leadership':
        return unit.leadership_cost ?? 0;
      case 'Dominance':
        return unit.dominance_cost ?? 0;
      case 'Authority':
      case 'Mercenary':
        return unit.authority_cost ?? 0;
      default:
        return 0;
    }
  }

  /**
   * Get strength per cost ratio (player units) or strength per health (enemy units)
   */
  static getEfficiencyRatio(unit: Unit): number {
    if (UnitUtils.isPlayerUnit(unit)) {
      const cost = UnitUtils.getPrimaryCost(unit);
      return cost > 0 ? unit.strength / cost : 0;
    } else {
      // For enemy units, use strength per health as efficiency metric
      return unit.health > 0 ? unit.strength / unit.health : 0;
    }
  }

  /**
   * Get strength per cost ratio (player units only)
   */
  static getStrengthPerCost(unit: Unit): number {
    const cost = UnitUtils.getPrimaryCost(unit);
    return cost > 0 ? unit.strength / cost : 0;
  }

  /**
   * Get health per cost ratio (player units only)
   */
  static getHealthPerCost(unit: Unit): number {
    const cost = UnitUtils.getPrimaryCost(unit);
    return cost > 0 ? unit.health / cost : 0;
  }

  /**
   * Get strength per health ratio (useful for enemy units)
   */
  static getStrengthPerHealth(unit: Unit): number {
    return unit.health > 0 ? unit.strength / unit.health : 0;
  }

  /**
   * Calculate effectiveness score based on unit type
   */
  static getEffectivenessScore(unit: Unit): number {
    if (UnitUtils.isPlayerUnit(unit)) {
      // For player units: factor in cost efficiency
      const cost = UnitUtils.getPrimaryCost(unit);
      return cost > 0 ? (unit.strength * unit.health) / cost : 0;
    } else {
      // For enemy units: simple strength * health calculation
      return (unit.strength * unit.health) / 1000;
    }
  }

  /**
   * Check if unit has specific unit type
   */
  static hasUnitType(unit: Unit, unitType: string): boolean {
    return unit.unit_types.some(type => 
      type.toLowerCase() === unitType.toLowerCase()
    );
  }

  /**
   * Get attack modifier value against specific target type
   */
  static getAttackModifierAgainst(unit: Unit, targetType: string): number {
    if (!unit.attack_modifiers) return 0;
    
    const modifier = unit.attack_modifiers.find(mod => 
      mod.target_type.toLowerCase() === targetType.toLowerCase()
    );
    
    return modifier ? modifier.value : 0;
  }

  /**
   * Calculate total strength including modifiers against target
   */
  static getTotalStrengthAgainst(unit: Unit, targetType: string): number {
    const baseStrength = unit.strength;
    const modifier = UnitUtils.getAttackModifierAgainst(unit, targetType);
    return baseStrength + modifier;
  }

  /**
   * Create a user enemy unit from base unit data
   */
  static createUserEnemyUnit(baseUnit: Partial<Unit>): Unit {
    const now = new Date();
    return {
      name: baseUnit.name ?? 'Unnamed Enemy',
      unit_types: baseUnit.unit_types ?? ['Epic Monster'],
      health: baseUnit.health ?? 10000,
      strength: baseUnit.strength ?? 5000,
      attack_modifiers: baseUnit.attack_modifiers ?? [],
      context: 'enemy',
      id: `user_enemy_${Date.now()}_${Math.random().toString(36).substr(2, 9)}`,
      createdAt: now,
      modifiedAt: now
    };
  }

  /**
   * Update a user unit's modified timestamp
   */
  static updateUserUnit(unit: Unit, updates: Partial<Unit>): Unit {
    return {
      ...unit,
      ...updates,
      modifiedAt: new Date()
    };
  }

  /**
   * Validate unit data structure
   */
  static validateUnit(unit: Partial<Unit>): { isValid: boolean; errors: string[]; warnings?: string[] } {
    const errors: string[] = [];
    const warnings: string[] = [];

    // Required fields validation
    if (!unit.name || unit.name.trim() === '') {
      errors.push('Unit name is required');
    }

    if (!unit.unit_types || !Array.isArray(unit.unit_types) || unit.unit_types.length === 0) {
      errors.push('At least one unit type is required');
    }

    if (typeof unit.health !== 'number' || unit.health <= 0) {
      errors.push('Health must be a positive number');
    }

    if (typeof unit.strength !== 'number' || unit.strength <= 0) {
      errors.push('Strength must be a positive number');
    }

    // Attack modifiers validation
    if (unit.attack_modifiers) {
      if (!Array.isArray(unit.attack_modifiers)) {
        errors.push('Attack modifiers must be an array');
      } else {
        unit.attack_modifiers.forEach((modifier, index) => {
          if (!modifier.target_type || modifier.target_type.trim() === '') {
            errors.push(`Attack modifier ${index + 1}: target_type is required`);
          }
          if (!modifier.modifier_type || modifier.modifier_type.trim() === '') {
            errors.push(`Attack modifier ${index + 1}: modifier_type is required`);
          }
          if (typeof modifier.value !== 'number' || modifier.value < 0) {
            errors.push(`Attack modifier ${index + 1}: value must be a non-negative number`);
          }
        });
      }
    }

    // Player unit specific validation
    if (unit.cost_type || unit.context === 'player') {
      if (unit.cost_type && !['Leadership', 'Dominance', 'Authority', 'Mercenary'].includes(unit.cost_type)) {
        errors.push('Invalid cost_type. Must be Leadership, Dominance, Authority, or Mercenary');
      }
    }

    // Warnings for unusual values
    if (unit.health && unit.health > 10000000) {
      warnings.push('Health value is unusually high');
    }

    if (unit.strength && unit.strength > 5000000) {
      warnings.push('Strength value is unusually high');
    }

    return {
      isValid: errors.length === 0,
      errors,
      warnings: warnings.length > 0 ? warnings : undefined
    };
  }

  /**
   * Validate enemy unit data structure (backward compatibility alias)
   * @deprecated Use validateUnit instead
   */
  static validateEnemyUnit(unit: Partial<Unit>): { isValid: boolean; errors: string[]; warnings?: string[] } {
    return UnitUtils.validateUnit(unit);
  }

  /**
   * Validate battle configuration constraints
   */
  static validateBattleConfiguration(config: {
    playerArmy?: any;
    enemyGroupCount?: number;
    enemyUnits?: Unit[];
  }): { isValid: boolean; errors: string[]; warnings?: string[] } {
    const errors: string[] = [];
    const warnings: string[] = [];

    // Validate player army
    if (!config.playerArmy) {
      errors.push('Player army is required');
    } else if (!config.playerArmy.stackingOrder || config.playerArmy.stackingOrder.length === 0) {
      errors.push('Player army must have at least one unit group');
    }

    // Validate enemy configuration
    if (config.enemyGroupCount !== undefined) {
      // Standard battle simulation validation
      if (config.enemyGroupCount < 1) {
        errors.push('Enemy group count must be at least 1');
      }
    }

    if (config.enemyUnits !== undefined) {
      // Enemy unit battle simulation validation
      if (!Array.isArray(config.enemyUnits) || config.enemyUnits.length === 0) {
        errors.push('At least one enemy unit is required');
      }
    }

    // At least one enemy configuration must be provided
    // For manual battle simulations, enemyUnits array may exist but be empty if conversion failed
    // Only require validation if neither enemy configuration is provided at all
    if (config.enemyGroupCount === undefined && config.enemyUnits === undefined) {
      errors.push('Either enemy group count or enemy units must be specified');
    } else if (config.enemyUnits !== undefined && config.enemyUnits.length === 0) {
      // If enemyUnits array is explicitly provided but empty, this is likely a conversion issue
      errors.push('Enemy units array is empty - check that enemy army has valid units selected');
    }

    return {
      isValid: errors.length === 0,
      errors,
      warnings: warnings.length > 0 ? warnings : undefined
    };
  }
}

