/**
 * Abstract Base Battle Simulation Service
 * Provides common battle simulation logic to eliminate code duplication
 * Implements the Template Method pattern for battle simulation workflow
 */

import {
  IBattleSimulationService,
  BattleConfiguration,
  BattleResult,
  BattleAnalysis,
  BattleState,
  CombatAction,
  BattleUtils,
  BATTLE_CONSTANTS
} from '../types/battleSimulation';
import { ArmyComposition, StackingGroup } from '../types/units';

/**
 * Abstract base class that implements common battle simulation logic
 * Concrete implementations extend this to provide specific battle mechanics
 */
export abstract class BaseBattleSimulationService implements IBattleSimulationService {
  // Shared caches for performance optimization
  protected attackOrderCache = new Map<string, StackingGroup[]>();
  protected targetOrderCache = new Map<string, StackingGroup[]>();

  /**
   * Template method - main battle simulation workflow
   * Concrete classes can override specific steps as needed
   */
  simulateBattle(config: BattleConfiguration): BattleResult {
    // Step 1: Validate configuration
    this.validateConfiguration(config);

    // Step 2: Initialize battle state
    const state = this.initializeBattleState(config);

    // Step 3: Run battle simulation loop
    this.runBattleLoop(state, config);

    // Step 4: Generate and return results
    return this.generateBattleResult(state, config);
  }

  /**
   * Standard implementation of both scenarios simulation
   * Most services can use this default implementation
   */
  simulateBothScenarios(playerArmy: ArmyComposition, enemyGroupCount: number): BattleAnalysis {
    // Best case: player attacks first (use original army)
    const bestCaseResult = this.simulateBattle({
      playerArmy,
      enemyGroupCount,
      playerGoesFirst: true
    });

    // Worst case: enemy attacks first (use cloned army to avoid mutation issues)
    const worstCaseResult = this.simulateBattle({
      playerArmy: JSON.parse(JSON.stringify(playerArmy)),
      enemyGroupCount,
      playerGoesFirst: false
    });

    // Calculate comparison metrics
    const comparison = {
      damageDifference: bestCaseResult.totalDamageDealtToEnemies - worstCaseResult.totalDamageDealtToEnemies,
      survivalDifference: bestCaseResult.playerSurvivalTurns - worstCaseResult.playerSurvivalTurns,
      averageDamage: (bestCaseResult.totalDamageDealtToEnemies + worstCaseResult.totalDamageDealtToEnemies) / 2,
      averageSurvival: (bestCaseResult.playerSurvivalTurns + worstCaseResult.playerSurvivalTurns) / 2
    };

    return {
      bestCase: bestCaseResult,
      worstCase: worstCaseResult,
      comparison
    };
  }

  /**
   * Standard attack order calculation (by strength, descending)
   * Can be overridden by concrete classes for different strategies
   */
  calculateAttackOrder(groups: StackingGroup[]): StackingGroup[] {
    const cacheKey = this.generateCacheKey(groups, 'attack');
    const cached = this.attackOrderCache.get(cacheKey);
    if (cached) return [...cached];

    const sorted = [...groups].sort((a, b) => b.unitStrength - a.unitStrength);
    this.attackOrderCache.set(cacheKey, sorted);
    return sorted;
  }

  /**
   * Standard enemy target order calculation (by health, ascending)
   * Can be overridden by concrete classes for different strategies
   */
  calculateEnemyTargetOrder(groups: StackingGroup[]): StackingGroup[] {
    const cacheKey = this.generateCacheKey(groups, 'target');
    const cached = this.targetOrderCache.get(cacheKey);
    if (cached) return [...cached];

    const sorted = [...groups].sort((a, b) => a.totalHealth - b.totalHealth);
    this.targetOrderCache.set(cacheKey, sorted);
    return sorted;
  }

  /**
   * Standard battle end condition
   * Can be overridden by concrete classes for different victory conditions
   */
  shouldBattleEnd(state: BattleState): boolean {
    return state.playerGroups.length === 0 || state.currentTurn >= BATTLE_CONSTANTS.MAX_BATTLE_TURNS || state.battleEnded;
  }

  /**
   * Standard turn processing logic
   * Can be overridden by concrete classes for different combat mechanics
   */
  processTurn(state: BattleState, playerGoesFirst: boolean): void {
    if (playerGoesFirst) {
      this.processPlayerActions(state);
      if (!this.shouldBattleEnd(state)) {
        this.processEnemyActions(state);
      }
    } else {
      this.processEnemyActions(state);
      if (!this.shouldBattleEnd(state)) {
        this.processPlayerActions(state);
      }
    }
    state.currentTurn++;
  }

  // === PROTECTED METHODS FOR SUBCLASS CUSTOMIZATION ===

  /**
   * Validate battle configuration - can be extended by subclasses
   */
  protected validateConfiguration(config: BattleConfiguration): void {
    if (!config) {
      throw new Error('Battle configuration is required');
    }

    if (!config.playerArmy) {
      throw new Error('Player army is required');
    }

    if (!config.playerArmy.stackingOrder || config.playerArmy.stackingOrder.length === 0) {
      throw new Error('Player army must have at least one unit group');
    }

    // Additional validation can be added by subclasses
  }

  /**
   * Initialize battle state - can be customized by subclasses
   */
  protected initializeBattleState(config: BattleConfiguration): BattleState {
    return {
      currentTurn: 0,
      playerGroups: [...config.playerArmy.stackingOrder],
      enemyGroupCount: this.getEnemyGroupCount(config),
      totalDamageDealt: 0,
      battleEnded: false,
      combatLog: []
    };
  }

  /**
   * Main battle loop - can be customized by subclasses
   */
  protected runBattleLoop(state: BattleState, config: BattleConfiguration): void {
    while (!this.shouldBattleEnd(state)) {
      this.processTurn(state, config.playerGoesFirst ?? true);
    }
  }

  /**
   * Generate final battle result - can be customized by subclasses
   */
  protected generateBattleResult(state: BattleState, config: BattleConfiguration): BattleResult {
    const statistics = BattleUtils.calculateBattleStatistics(state.combatLog, state.totalDamageDealt);

    return {
      outcome: 'player_eliminated',
      combatLog: state.combatLog,
      totalDamageDealtToEnemies: state.totalDamageDealt,
      battleDuration: state.currentTurn,
      playerSurvivalTurns: state.currentTurn,
      scenario: config.playerGoesFirst ? 'best_case' : 'worst_case',
      configuration: config,
      statistics
    };
  }

  // === ABSTRACT METHODS FOR SUBCLASS IMPLEMENTATION ===

  /**
   * Get enemy group count from configuration
   * Abstract because different services handle this differently
   */
  protected abstract getEnemyGroupCount(config: BattleConfiguration): number;

  /**
   * Calculate initial enemy health
   * Abstract because different services have different enemy health calculations
   */
  protected abstract calculateInitialEnemyHealth(config: BattleConfiguration): number;

  /**
   * Process player attack actions
   * Abstract because different services have different attack mechanics
   */
  protected abstract processPlayerActions(state: BattleState): void;

  /**
   * Process enemy attack actions
   * Abstract because different services have different enemy mechanics
   */
  protected abstract processEnemyActions(state: BattleState): void;

  // === PRIVATE UTILITY METHODS ===

  /**
   * Generate cache key for attack/target order caching
   */
  protected generateCacheKey(groups: StackingGroup[], type: 'attack' | 'target'): string {
    return `${type}-${groups.map(g => `${g.unitName}:${g.count}:${g.totalHealth}:${g.unitStrength}`).join('|')}`;
  }
}