/**
 * Battle Simulation Service
 * Handles turn-based combat simulation with asymmetric mechanics
 * - Enemy attacks eliminate player groups completely
 * - Player attacks deal damage to enemies but never eliminate them
 * - Battle ends when all player groups are eliminated
 */

import {
  IBattleSimulationService,
  BattleConfiguration,
  BattleResult,
  BattleAnalysis,
  BattleState,
  CombatAction,
  BattleUtils,
  BATTLE_CONSTANTS
} from '../types/battleSimulation';
import { ArmyComposition, StackingGroup, UnitUtils } from '../types/units';
import { BaseBattleSimulationService } from './baseBattleSimulation';

export class BattleSimulationService extends BaseBattleSimulationService {
  
  /**
   * Enhanced validation specific to this service
   */
  protected validateConfiguration(config: BattleConfiguration): void {
    // Call parent validation first
    super.validateConfiguration(config);

    // Use centralized validation
    const validation = UnitUtils.validateBattleConfiguration({
      playerArmy: config.playerArmy,
      enemyGroupCount: config.enemyGroupCount
    });

    if (!validation.isValid) {
      throw new Error(validation.errors[0]); // Throw first error
    }

    // Additional unit group validation
    for (const group of config.playerArmy.stackingOrder) {
      if (group.count <= 0) {
        throw new Error(`Unit group "${group.unitName}" must have a positive count`);
      }

      if (group.unitStrength <= 0) {
        throw new Error(`Unit group "${group.unitName}" must have positive strength`);
      }

      if (group.totalHealth <= 0) {
        throw new Error(`Unit group "${group.unitName}" must have positive health`);
      }
    }
  }

  // === ABSTRACT METHOD IMPLEMENTATIONS ===

  /**
   * Get enemy group count from configuration
   */
  protected getEnemyGroupCount(config: BattleConfiguration): number {
    return config.enemyGroupCount;
  }

  /**
   * Calculate initial enemy health (not applicable in this implementation)
   */
  protected calculateInitialEnemyHealth(config: BattleConfiguration): number {
    return 0; // This service doesn't track enemy health
  }

  /**
   * Override to add additional validation and cache clearing for scenarios
   */
  simulateBothScenarios(playerArmy: ArmyComposition, enemyGroupCount: number): BattleAnalysis {
    // Use centralized validation
    const validation = UnitUtils.validateBattleConfiguration({
      playerArmy,
      enemyGroupCount
    });

    if (!validation.isValid) {
      throw new Error(validation.errors[0]); // Throw first error
    }

    // Clear caches to ensure clean state between scenarios
    this.attackOrderCache.clear();
    this.targetOrderCache.clear();

    // Use base implementation
    const result = super.simulateBothScenarios(playerArmy, enemyGroupCount);

    // Clear caches again after scenarios to prevent stale data
    this.attackOrderCache.clear();
    this.targetOrderCache.clear();

    return result;
  }

  /**
   * Override base implementation to calculate attack order by maximum damage potential
   * Units that can deal the most damage to enemies attack first
   * Note: This is a basic implementation for standard battle simulation
   * Enhanced logic is in EnemyUnitBattleSimulationService for specific enemy types
   */
  calculateAttackOrder(groups: StackingGroup[]): StackingGroup[] {
    const cacheKey = this.generateCacheKey(groups, 'attack');
    const cached = this.attackOrderCache.get(cacheKey);
    if (cached) return [...cached];

    // Sort by maximum damage potential (count × unitStrength), highest first
    // This represents the damage this group can deal to enemies
    const sorted = [...groups].sort((a, b) => {
      const damageA = BattleUtils.calculateDamageDealt(a);
      const damageB = BattleUtils.calculateDamageDealt(b);
      return damageB - damageA;
    });
    this.attackOrderCache.set(cacheKey, sorted);
    return sorted;
  }

  /**
   * Calculate optimal enemy attack order by damage potential
   * Returns array of enemy indices sorted by maximum damage potential
   */
  private calculateEnemyAttackOrder(state: BattleState): number[] {
    // For basic battle simulation, we don't have specific enemy units
    // So we sort by enemy group index (assumes enemies are ordered by strength in input)
    // This is a placeholder - the real logic should be in EnemyUnitBattleSimulationService
    const enemyIndices = Array.from({ length: state.enemyGroupCount }, (_, i) => i);
    
    // For now, reverse order (assume higher index = stronger enemy)
    // This is a basic heuristic since we don't have enemy unit data in this service
    return enemyIndices.reverse();
  }

  /**
   * Generate cache key for attack/target order caching (override base implementation)
   */
  protected generateCacheKey(groups: StackingGroup[], type: 'attack' | 'target'): string {
    return `${type}-${groups.map(g => `${g.unitName}:${g.count}:${g.totalHealth}:${g.unitStrength}`).join('|')}`;
  }

  /**
   * Override base implementation to target highest health first
   */
  calculateEnemyTargetOrder(groups: StackingGroup[]): StackingGroup[] {
    const cacheKey = this.generateCacheKey(groups, 'target');
    const cached = this.targetOrderCache.get(cacheKey);
    if (cached) return [...cached];

    // Sort by total health, highest first (different from base implementation)
    const sorted = [...groups].sort((a, b) => b.totalHealth - a.totalHealth);
    this.targetOrderCache.set(cacheKey, sorted);
    return sorted;
  }

  /**
   * Override base implementation to remove turn limit check
   */
  shouldBattleEnd(state: BattleState): boolean {
    return state.playerGroups.length === 0 || state.battleEnded;
  }

  /**
   * Override base class turn processing to provide true individual unit alternation
   * Each side (player/enemy) gets alternating turns, using their strongest available unit
   */
  processTurn(state: BattleState, playerGoesFirst: boolean): void {
    console.log(`🔥 BattleSimulationService.processTurn called - playerGoesFirst: ${playerGoesFirst}, turn: ${state.currentTurn}`);
    
    // If no player groups, end the battle
    if (state.playerGroups.length === 0) {
      state.battleEnded = true;
      return;
    }

    // Track attacks this turn
    let playerTurnsTaken = 0;
    let enemyTurnsTaken = 0;
    const maxEnemyTurns = state.enemyGroupCount;

    // Calculate enemy attack order by damage potential (like players)
    const enemyAttackOrder = this.calculateEnemyAttackOrder(state);

    // Keep alternating until both sides have taken all their turns for this round
    // Each side gets equal number of actions (based on unit count)
    const initialPlayerCount = state.playerGroups.length;
    const maxPlayerTurns = initialPlayerCount;
    const totalPossibleActions = maxPlayerTurns + maxEnemyTurns;
    
    for (let actionIndex = 0; actionIndex < totalPossibleActions && !this.shouldBattleEnd(state); actionIndex++) {
      // Determine whose turn it is (true alternation based on who goes first)
      const isPlayerAction = playerGoesFirst ? (actionIndex % 2 === 0) : (actionIndex % 2 === 1);
      
      if (isPlayerAction && playerTurnsTaken < maxPlayerTurns && state.playerGroups.length > 0) {
        // PLAYER'S TURN: Find the strongest remaining player unit and attack with it
        console.log(`  Action ${actionIndex}: PLAYER turn (${playerTurnsTaken + 1}/${maxPlayerTurns})`);
        
        // Get current strongest player unit (recalculate each time as units may be eliminated)
        const currentPlayerAttackOrder = this.calculateAttackOrder(state.playerGroups);
        if (currentPlayerAttackOrder.length > 0) {
          const strongestPlayer = currentPlayerAttackOrder[0]; // First in order = strongest
          console.log(`    → ${strongestPlayer.unitName} attacks (Turn ${state.currentTurn})`);
          this.processSinglePlayerAttackByGroup(state, strongestPlayer, playerTurnsTaken);
        }
        playerTurnsTaken++;
        
      } else if (!isPlayerAction && enemyTurnsTaken < maxEnemyTurns && state.playerGroups.length > 0) {
        // ENEMY'S TURN: Use calculated attack order for optimal damage
        console.log(`  Action ${actionIndex}: ENEMY turn (${enemyTurnsTaken + 1}/${maxEnemyTurns})`);
        const optimalEnemyIndex = enemyAttackOrder[enemyTurnsTaken];
        console.log(`    → Enemy Group ${optimalEnemyIndex + 1} attacks (Turn ${state.currentTurn})`);
        this.processSingleEnemyAttack(state, optimalEnemyIndex);
        enemyTurnsTaken++;
      }

      // Safety check: if all players are dead, end the turn
      if (state.playerGroups.length === 0) {
        state.battleEnded = true;
        break;
      }
      
      // If both sides have finished all their attacks for this turn, we're done
      if (playerTurnsTaken >= maxPlayerTurns && enemyTurnsTaken >= maxEnemyTurns) {
        break;
      }
    }
    
    // Increment turn after processing all actions (matches base class behavior)
    state.currentTurn++;
  }

  /**
   * Implement abstract method: Process player attack actions
   * NOTE: This should NOT be called when using the overridden processTurn method
   */
  protected processPlayerActions(state: BattleState): void {
    console.error('🚨 ERROR: processPlayerActions called instead of processTurn override!');
    throw new Error('processPlayerActions should not be called - using overridden processTurn instead');
  }

  /**
   * Implement abstract method: Process enemy attack actions  
   * NOTE: This should NOT be called when using the overridden processTurn method
   */
  protected processEnemyActions(state: BattleState): void {
    console.error('🚨 ERROR: processEnemyActions called instead of processTurn override!');
    throw new Error('processEnemyActions should not be called - using overridden processTurn instead');
  }

  // === PRIVATE HELPER METHODS ===






  /**
   * Process a single player unit's turn (one attack) for a specific group
   */
  private processSinglePlayerAttackByGroup(state: BattleState, attackingGroup: StackingGroup, attackIndex: number): void {
    if (state.playerGroups.length === 0) return;

    const targetEnemyIndex = attackIndex % state.enemyGroupCount;

    // Calculate damage and create combat action
    const damage = BattleUtils.calculateDamageDealt(attackingGroup);
    const action = BattleUtils.createPlayerAttackAction(
      state.currentTurn,
      attackingGroup,
      targetEnemyIndex
    );

    // Add to combat log and track damage
    console.log(`📝 Adding PLAYER action to combat log: ${action.action} (Turn ${action.turn})`);
    state.combatLog.push(action);
    state.totalDamageDealt += damage;
  }


  /**
   * Process a single enemy unit's turn (one attack)
   */
  private processSingleEnemyAttack(state: BattleState, enemyGroupIndex: number): void {
    if (state.playerGroups.length === 0) return;

    // Get current targeting order for remaining player groups (highest health first)
    const currentTargetOrder = this.calculateEnemyTargetOrder(state.playerGroups);

    // Enemy always targets the highest health player group (index 0)
    if (currentTargetOrder.length === 0) return;

    const targetGroup = currentTargetOrder[0]; // Always target highest health group

    // Create combat action
    const action = BattleUtils.createEnemyAttackAction(
      state.currentTurn,
      enemyGroupIndex, // Which enemy group is attacking
      targetGroup
    );

    // Add to combat log
    console.log(`📝 Adding ENEMY action to combat log: ${action.action} (Turn ${action.turn})`);
    state.combatLog.push(action);

    // Remove the eliminated group from player groups
    const groupIndex = state.playerGroups.findIndex(
      group => group.unitName === targetGroup.unitName &&
               group.count === targetGroup.count &&
               group.totalHealth === targetGroup.totalHealth
    );

    if (groupIndex !== -1) {
      state.playerGroups.splice(groupIndex, 1);
    }

    // Check if battle should end
    if (state.playerGroups.length === 0) {
      state.battleEnded = true;
    }
  }
}
