/**
 * Attack Order Optimizer Service
 * Implements intelligent attack order optimization based on maximum damage calculations
 * Supports both player and enemy unit optimization with unit-vs-unit modifiers
 */

import {
  CombatUnit,
  UnitSide,
  DamageCalculation,
  DamageMatrix,
  AttackOrderOptimization,
  BidirectionalOptimization,
  OptimizationConfig,
  ModifierApplication,
  OptimizationMetrics,
  CombatUnitUtils,
  DEFAULT_OPTIMIZATION_CONFIG,
  ATTACK_OPTIMIZATION_CONSTANTS
} from '../types/attackOptimization';
import { StackingGroup } from '../types/units';
import { EnemyUnit, EnemyUnitUtils } from '../types/enemyUnits';
import { unitTypeMappingService } from '../utils/unitTypeMapping';

export class AttackOrderOptimizerService {
  private damageCalculationCache = new Map<string, number>();
  private modifierCache = new Map<string, ModifierApplication>();
  private metrics: OptimizationMetrics = this.initializeMetrics();

  /**
   * Initialize performance metrics
   */
  private initializeMetrics(): OptimizationMetrics {
    return {
      totalUnitsProcessed: 0,
      totalCalculations: 0,
      cacheHitRate: 0,
      memoryUsageBytes: 0,
      timeBreakdown: {
        damageCalculation: 0,
        matrixGeneration: 0,
        optimization: 0,
        caching: 0
      }
    };
  }

  /**
   * Optimize attack order for both player and enemy units
   */
  async optimizeBidirectional(
    playerUnits: StackingGroup[],
    enemyUnits: EnemyUnit[],
    config: OptimizationConfig = DEFAULT_OPTIMIZATION_CONFIG
  ): Promise<BidirectionalOptimization> {
    const startTime = performance.now();

    // Reset metrics for this optimization
    this.metrics = this.initializeMetrics();

    let playerOptimization: AttackOrderOptimization;
    let enemyOptimization: AttackOrderOptimization;

    // Optimize player attack order (attacking enemies)
    if (config.optimizePlayerOrder) {
      playerOptimization = await this.optimizeAttackOrder(
        playerUnits,
        enemyUnits,
        'player',
        config
      );
    } else {
      playerOptimization = this.createNoOptimizationResult(playerUnits, enemyUnits, 'player');
    }

    // Optimize enemy attack order (attacking players)
    if (config.optimizeEnemyOrder) {
      enemyOptimization = await this.optimizeAttackOrder(
        enemyUnits,
        playerUnits,
        'enemy',
        config
      );
    } else {
      enemyOptimization = this.createNoOptimizationResult(enemyUnits, playerUnits, 'enemy');
    }

    const totalTime = performance.now() - startTime;

    return {
      playerOptimization,
      enemyOptimization,
      combinedMetrics: {
        totalDamageImprovement: 
          playerOptimization.optimizedTotalDamage - playerOptimization.originalTotalDamage +
          enemyOptimization.optimizedTotalDamage - enemyOptimization.originalTotalDamage,
        averageImprovementPercentage: 
          (playerOptimization.improvementPercentage + enemyOptimization.improvementPercentage) / 2,
        totalOptimizationTimeMs: totalTime
      }
    };
  }

  /**
   * Optimize attack order for one side
   */
  private async optimizeAttackOrder(
    attackers: CombatUnit[],
    targets: CombatUnit[],
    side: UnitSide,
    config: OptimizationConfig
  ): Promise<AttackOrderOptimization> {
    const startTime = performance.now();

    // Generate damage matrix
    const damageMatrix = await this.generateDamageMatrix(attackers, targets, side, config);

    // Calculate original total damage
    const originalTotalDamage = this.calculateTotalDamage(attackers, targets, damageMatrix);

    // Optimize attack order
    const optimizedOrder = this.optimizeOrder(attackers, targets, damageMatrix, config);

    // Calculate optimized total damage
    const optimizedTotalDamage = this.calculateTotalDamage(optimizedOrder, targets, damageMatrix);

    const optimizationTime = performance.now() - startTime;
    const improvementPercentage = originalTotalDamage > 0 
      ? ((optimizedTotalDamage - originalTotalDamage) / originalTotalDamage) * 100 
      : 0;

    return {
      originalOrder: [...attackers],
      optimizedOrder,
      damageMatrix,
      originalTotalDamage,
      optimizedTotalDamage,
      improvementPercentage,
      side,
      optimizationTimeMs: optimizationTime
    };
  }

  /**
   * Generate damage matrix for all attacker/target combinations
   */
  private async generateDamageMatrix(
    attackers: CombatUnit[],
    targets: CombatUnit[],
    attackerSide: UnitSide,
    config: OptimizationConfig
  ): Promise<DamageMatrix> {
    const startTime = performance.now();
    const calculations: DamageCalculation[][] = [];

    for (let i = 0; i < attackers.length; i++) {
      calculations[i] = [];
      for (let j = 0; j < targets.length; j++) {
        const attacker = attackers[i];
        const target = targets[j];
        
        calculations[i][j] = await this.calculateEffectiveDamage(
          attacker,
          target,
          attackerSide,
          attackerSide === 'player' ? 'enemy' : 'player',
          config
        );
        
        this.metrics.totalCalculations++;
      }
    }

    const calculationTime = performance.now() - startTime;
    this.metrics.timeBreakdown.matrixGeneration += calculationTime;

    return {
      calculations,
      attackers: [...attackers],
      targets: [...targets],
      calculationTimeMs: calculationTime
    };
  }

  /**
   * Calculate effective damage between an attacker and target
   */
  private async calculateEffectiveDamage(
    attacker: CombatUnit,
    target: CombatUnit,
    attackerSide: UnitSide,
    targetSide: UnitSide,
    config: OptimizationConfig
  ): Promise<DamageCalculation> {
    const startTime = performance.now();

    // Calculate base damage
    const baseDamage = CombatUnitUtils.calculateBaseDamage(attacker);

    let effectiveDamage = baseDamage;
    const modifiersApplied: Array<{ type: string; value: number; source: string }> = [];

    // Apply unit-vs-unit modifiers if enabled
    if (config.useUnitModifiers) {
      const modifierResult = await this.applyUnitModifiers(attacker, target, baseDamage);
      effectiveDamage = modifierResult.modifiedValue;
      modifiersApplied.push(...modifierResult.appliedModifiers);
    }

    // Cap effective damage to target's health pool
    // Units cannot take more damage than their total health
    const targetHealth = CombatUnitUtils.calculateTotalHealth(target);
    const cappedEffectiveDamage = Math.min(effectiveDamage, targetHealth);

    this.metrics.timeBreakdown.damageCalculation += performance.now() - startTime;

    return {
      attacker,
      target,
      baseDamage,
      effectiveDamage: cappedEffectiveDamage,
      modifiersApplied,
      attackerSide,
      targetSide
    };
  }

  /**
   * Apply unit-vs-unit modifiers to damage calculation
   */
  private async applyUnitModifiers(
    attacker: CombatUnit,
    target: CombatUnit,
    baseDamage: number
  ): Promise<ModifierApplication> {
    // Create cache key
    const attackerName = CombatUnitUtils.getUnitName(attacker);
    const targetName = CombatUnitUtils.getUnitName(target);
    const cacheKey = `${attackerName}:${targetName}:${baseDamage}`;

    // Check cache first
    if (this.modifierCache.has(cacheKey)) {
      return this.modifierCache.get(cacheKey)!;
    }

    let modifiedValue = baseDamage;
    const appliedModifiers: Array<{ type: string; value: number; source: string }> = [];

    // Get attacker and target types
    const targetTypes = CombatUnitUtils.isEnemyUnit(target)
      ? target.unit_types
      : this.getPlayerUnitTypes(target.unitName);

    // Apply modifiers based on unit types
    if (CombatUnitUtils.isEnemyUnit(attacker)) {
      // Enemy attacking player - use enemy's attack modifiers
      const enemyUnit = attacker as EnemyUnit;
      if (enemyUnit.attack_modifiers) {
        for (const modifier of enemyUnit.attack_modifiers) {
          // Check if target has the type this modifier affects
          if (targetTypes.some(type => type.toLowerCase() === modifier.target_type.toLowerCase())) {
            // Apply percentage modifier: value is percentage increase
            // modifier.value is already the bonus damage amount (e.g., 56000 means +56000 damage)
            const bonusDamage = modifier.value;
            modifiedValue += bonusDamage;
            appliedModifiers.push({
              type: `vs ${modifier.target_type}`,
              value: bonusDamage,
              source: enemyUnit.name
            });
          }
        }
      }
    } else if (CombatUnitUtils.isEnemyUnit(target)) {
      // Player attacking enemy - use player's attack modifiers
      const playerUnit = await this.getPlayerUnitData(attacker.unitName);
      if (playerUnit && playerUnit.attack_modifiers) {
        for (const modifier of playerUnit.attack_modifiers) {
          // Check if target has the type this modifier affects
          if (targetTypes.some(type => type.toLowerCase() === modifier.target_type.toLowerCase())) {
            // Apply percentage modifier: value is already the bonus damage amount
            const bonusDamage = modifier.value;
            modifiedValue += bonusDamage;
            appliedModifiers.push({
              type: `vs ${modifier.target_type}`,
              value: bonusDamage,
              source: attacker.unitName
            });
          }
        }
      }
    }

    // Ensure damage doesn't go negative
    modifiedValue = Math.max(0, modifiedValue);

    const result: ModifierApplication = {
      baseValue: baseDamage,
      modifiedValue,
      appliedModifiers,
      hasModifiers: appliedModifiers.length > 0
    };

    // Cache the result
    if (this.modifierCache.size < ATTACK_OPTIMIZATION_CONSTANTS.MAX_CACHE_SIZE) {
      this.modifierCache.set(cacheKey, result);
    }

    return result;
  }

  /**
   * Optimize the attack order using the specified algorithm
   */
  private optimizeOrder(
    attackers: CombatUnit[],
    targets: CombatUnit[],
    damageMatrix: DamageMatrix,
    config: OptimizationConfig
  ): CombatUnit[] {
    const startTime = performance.now();

    let optimizedOrder: CombatUnit[];

    switch (config.algorithm) {
      case 'greedy':
        optimizedOrder = this.greedyOptimization(attackers, targets, damageMatrix);
        break;
      case 'dynamic':
        optimizedOrder = this.dynamicOptimization(attackers, targets, damageMatrix);
        break;
      case 'heuristic':
        optimizedOrder = this.heuristicOptimization(attackers, targets, damageMatrix);
        break;
      default:
        optimizedOrder = this.greedyOptimization(attackers, targets, damageMatrix);
    }

    this.metrics.timeBreakdown.optimization += performance.now() - startTime;
    return optimizedOrder;
  }

  /**
   * Greedy optimization: always select the unit that deals maximum damage
   */
  private greedyOptimization(
    attackers: CombatUnit[],
    targets: CombatUnit[],
    damageMatrix: DamageMatrix
  ): CombatUnit[] {
    const optimizedOrder: CombatUnit[] = [];
    const remainingAttackers = [...attackers];

    while (remainingAttackers.length > 0) {
      let maxDamage = -1;
      let bestAttackerIndex = 0;

      // Find the attacker that deals maximum damage to any target
      for (let i = 0; i < remainingAttackers.length; i++) {
        const attackerIndex = attackers.indexOf(remainingAttackers[i]);
        
        // Calculate total damage this attacker would deal to all targets
        let totalDamage = 0;
        for (let j = 0; j < targets.length; j++) {
          totalDamage += damageMatrix.calculations[attackerIndex][j].effectiveDamage;
        }

        if (totalDamage > maxDamage) {
          maxDamage = totalDamage;
          bestAttackerIndex = i;
        }
      }

      // Add the best attacker to optimized order and remove from remaining
      optimizedOrder.push(remainingAttackers[bestAttackerIndex]);
      remainingAttackers.splice(bestAttackerIndex, 1);
    }

    return optimizedOrder;
  }

  /**
   * Dynamic optimization: consider target elimination and remaining health
   */
  private dynamicOptimization(
    attackers: CombatUnit[],
    targets: CombatUnit[],
    damageMatrix: DamageMatrix
  ): CombatUnit[] {
    // For now, fall back to greedy optimization
    // This could be enhanced with more sophisticated algorithms
    return this.greedyOptimization(attackers, targets, damageMatrix);
  }

  /**
   * Heuristic optimization: use simple rules for large armies
   */
  private heuristicOptimization(
    attackers: CombatUnit[],
    targets: CombatUnit[],
    damageMatrix: DamageMatrix
  ): CombatUnit[] {
    // For large armies, use a simpler heuristic approach
    if (attackers.length > ATTACK_OPTIMIZATION_CONSTANTS.MAX_UNITS_FOR_EXACT_OPTIMIZATION) {
      // Sort by base damage (simple but fast)
      return [...attackers].sort((a, b) => 
        CombatUnitUtils.calculateBaseDamage(b) - CombatUnitUtils.calculateBaseDamage(a)
      );
    } else {
      // Use greedy for smaller armies
      return this.greedyOptimization(attackers, targets, damageMatrix);
    }
  }

  /**
   * Calculate total damage for a given attack order
   */
  private calculateTotalDamage(
    attackers: CombatUnit[],
    targets: CombatUnit[],
    damageMatrix: DamageMatrix
  ): number {
    let totalDamage = 0;

    for (let i = 0; i < attackers.length; i++) {
      const attackerIndex = damageMatrix.attackers.indexOf(attackers[i]);
      if (attackerIndex >= 0) {
        for (let j = 0; j < targets.length; j++) {
          totalDamage += damageMatrix.calculations[attackerIndex][j].effectiveDamage;
        }
      }
    }

    return totalDamage;
  }

  /**
   * Create a no-optimization result (original order)
   */
  private createNoOptimizationResult(
    attackers: CombatUnit[],
    targets: CombatUnit[],
    side: UnitSide
  ): AttackOrderOptimization {
    const emptyMatrix: DamageMatrix = {
      calculations: [],
      attackers: [...attackers],
      targets: [...targets],
      calculationTimeMs: 0
    };

    return {
      originalOrder: [...attackers],
      optimizedOrder: [...attackers],
      damageMatrix: emptyMatrix,
      originalTotalDamage: 0,
      optimizedTotalDamage: 0,
      improvementPercentage: 0,
      side,
      optimizationTimeMs: 0
    };
  }

  /**
   * Get current performance metrics
   */
  getMetrics(): OptimizationMetrics {
    // Calculate cache hit rate
    const totalCacheAccess = this.metrics.totalCalculations;
    const cacheHits = this.damageCalculationCache.size + this.modifierCache.size;
    this.metrics.cacheHitRate = totalCacheAccess > 0 ? cacheHits / totalCacheAccess : 0;

    // Estimate memory usage
    this.metrics.memoryUsageBytes = 
      (this.damageCalculationCache.size * 64) + // Rough estimate for cache entries
      (this.modifierCache.size * 128);

    return { ...this.metrics };
  }

  /**
   * Clear all caches
   */
  clearCache(): void {
    this.damageCalculationCache.clear();
    this.modifierCache.clear();
  }

  /**
   * Get unit types for a player unit by looking up the full Unit object
   */
  private getPlayerUnitTypes(unitName: string): string[] {
    return unitTypeMappingService.getUnitTypes(unitName);
  }

  /**
   * Get full player unit data including attack modifiers
   */
  private async getPlayerUnitData(unitName: string): Promise<any> {
    return unitTypeMappingService.getFullUnitData(unitName);
  }

  /**
   * Initialize the service with unit loader
   */
  initialize(unitLoader: any): void {
    unitTypeMappingService.initialize(unitLoader);
  }
}

/**
 * Singleton instance of the attack order optimizer service
 */
export const attackOrderOptimizerService = new AttackOrderOptimizerService();