/**
 * Optimized Battle Simulation Service
 * Integrates intelligent attack order optimization with existing battle simulation
 * Supports both basic enemy groups and specific enemy units with bidirectional optimization
 */

import {
  IBattleSimulationService,
  BattleConfiguration,
  BattleResult,
  BattleAnalysis,
  BattleState,
  CombatAction,
  BattleUtils,
  BATTLE_CONSTANTS
} from '../types/battleSimulation';
import { ArmyComposition, StackingGroup } from '../types/units';
import { EnemyUnit } from '../types/enemyUnits';
import { BattleSimulationService } from './battleSimulation';
import { EnemyUnitBattleSimulationService, EnemyUnitBattleConfiguration } from './enemyBattleSimulation';
import { attackOrderOptimizerService } from './attackOrderOptimizer';
import { unitTypeMappingService } from '../utils/unitTypeMapping';
import {
  OptimizationConfig,
  BidirectionalOptimization,
  DEFAULT_OPTIMIZATION_CONFIG,
  CombatUnitUtils
} from '../types/attackOptimization';

/**
 * Enhanced battle configuration with optimization options
 */
export interface OptimizedBattleConfiguration extends BattleConfiguration {
  /** Attack order optimization configuration */
  optimizationConfig?: OptimizationConfig;
  /** Whether to use attack order optimization */
  useOptimization?: boolean;
}

/**
 * Enhanced enemy unit battle configuration with optimization
 */
export interface OptimizedEnemyUnitBattleConfiguration extends EnemyUnitBattleConfiguration {
  /** Attack order optimization configuration */
  optimizationConfig?: OptimizationConfig;
  /** Whether to use attack order optimization */
  useOptimization?: boolean;
}

/**
 * Enhanced battle result with optimization data
 */
export interface OptimizedBattleResult extends BattleResult {
  /** Optimization results if optimization was used */
  optimizationResult?: BidirectionalOptimization;
  /** Whether optimization was applied */
  optimizationUsed: boolean;
  /** Performance metrics from optimization */
  optimizationMetrics?: any;
}

export class OptimizedBattleSimulationService extends EnemyUnitBattleSimulationService {
  private optimizationCache = new Map<string, BidirectionalOptimization>();

  /**
   * Initialize the service with unit loader
   */
  initialize(unitLoader: any): void {
    super.initialize(unitLoader);
    attackOrderOptimizerService.initialize(unitLoader);
  }

  /**
   * Simulate battle with optimization support (synchronous version)
   */
  simulateOptimizedBattle(config: OptimizedBattleConfiguration): OptimizedBattleResult {
    // If optimization is disabled, fall back to standard simulation
    if (!config.useOptimization) {
      const standardResult = this.simulateBattle(config);
      return {
        ...standardResult,
        optimizationUsed: false
      };
    }

    // Create abstract enemy units for optimization
    const abstractEnemyUnits: EnemyUnit[] = Array.from({ length: config.enemyGroupCount }, (_, i) => ({
      name: `Enemy Group ${i + 1}`,
      unit_types: ['Generic'],
      health: 1000000, // High health since enemies don't die in this system
      strength: 1000,   // Generic strength
      attack_modifiers: []
    }));

    return this.simulateOptimizedBattleWithEnemyUnits({
      ...config,
      enemyUnits: abstractEnemyUnits,
      optimizationConfig: config.optimizationConfig || DEFAULT_OPTIMIZATION_CONFIG,
      useOptimization: true
    });
  }

  /**
   * Simulate battle with specific enemy units and optimization (synchronous version)
   */
  simulateOptimizedBattleWithEnemyUnits(config: OptimizedEnemyUnitBattleConfiguration): OptimizedBattleResult {
    // If optimization is disabled, fall back to standard enemy unit simulation
    if (!config.useOptimization) {
      const standardResult = this.simulateBattleWithEnemyUnits(config);
      return {
        ...standardResult,
        optimizationUsed: false
      };
    }

    const optimizationConfig = config.optimizationConfig || DEFAULT_OPTIMIZATION_CONFIG;

    // Create cache key for optimization
    const cacheKey = this.createOptimizationCacheKey(config.playerArmy.stackingOrder, config.enemyUnits, optimizationConfig);

    // Check cache first
    let optimizationResult: BidirectionalOptimization | undefined;
    if (this.optimizationCache.has(cacheKey)) {
      optimizationResult = this.optimizationCache.get(cacheKey);
    } else {
      // Perform optimization analysis (synchronous fallback)
      try {
        // For now, we'll use a simplified optimization that doesn't require async
        optimizationResult = this.performSynchronousOptimization(
          config.playerArmy.stackingOrder,
          config.enemyUnits,
          optimizationConfig
        );
        
        // Cache the result
        if (this.optimizationCache.size < 100) { // Limit cache size
          this.optimizationCache.set(cacheKey, optimizationResult);
        }
      } catch (error) {
        console.warn('Attack order optimization failed, falling back to standard simulation:', error);
        const standardResult = this.simulateBattleWithEnemyUnits(config);
        return {
          ...standardResult,
          optimizationUsed: false
        };
      }
    }

    // Run simulation with optimized attack orders
    const optimizedConfig: EnemyUnitBattleConfiguration = {
      ...config,
      playerArmy: {
        ...config.playerArmy,
        stackingOrder: optimizationResult?.playerOptimization.optimizedOrder as StackingGroup[] || config.playerArmy.stackingOrder
      }
    };

    // Simulate with optimized configuration
    const battleResult = this.simulateBattleWithEnemyUnits(optimizedConfig);

    // Get optimization metrics
    const optimizationMetrics = attackOrderOptimizerService.getMetrics();

    return {
      ...battleResult,
      optimizationResult,
      optimizationUsed: true,
      optimizationMetrics
    };
  }

  /**
   * Perform synchronous optimization (simplified version)
   */
  private performSynchronousOptimization(
    playerUnits: StackingGroup[],
    enemyUnits: EnemyUnit[],
    config: OptimizationConfig
  ): BidirectionalOptimization {
    // Simplified optimization that works synchronously
    // This is a basic implementation that sorts by damage potential

    const playerOptimization = this.optimizePlayerOrder(playerUnits, enemyUnits, config);
    const enemyOptimization = this.optimizeEnemyOrder(enemyUnits, playerUnits, config);

    return {
      playerOptimization,
      enemyOptimization,
      combinedMetrics: {
        totalDamageImprovement: 
          playerOptimization.optimizedTotalDamage - playerOptimization.originalTotalDamage +
          enemyOptimization.optimizedTotalDamage - enemyOptimization.originalTotalDamage,
        averageImprovementPercentage: 
          (playerOptimization.improvementPercentage + enemyOptimization.improvementPercentage) / 2,
        totalOptimizationTimeMs: 
          playerOptimization.optimizationTimeMs + enemyOptimization.optimizationTimeMs
      }
    };
  }

  /**
   * Optimize player attack order (simplified synchronous version)
   */
  private optimizePlayerOrder(
    playerUnits: StackingGroup[],
    enemyUnits: EnemyUnit[],
    config: OptimizationConfig
  ): any {
    const startTime = performance.now();

    if (!config.optimizePlayerOrder) {
      return this.createNoOptimizationResult(playerUnits, 'player', startTime);
    }

    // Simple optimization: sort by total damage potential
    const optimizedOrder = [...playerUnits].sort((a, b) => {
      const damageA = this.calculateSimpleDamage(a, enemyUnits);
      const damageB = this.calculateSimpleDamage(b, enemyUnits);
      return damageB - damageA;
    });

    const originalTotalDamage = this.calculateTotalSimpleDamage(playerUnits, enemyUnits);
    const optimizedTotalDamage = this.calculateTotalSimpleDamage(optimizedOrder, enemyUnits);
    const improvementPercentage = originalTotalDamage > 0 
      ? ((optimizedTotalDamage - originalTotalDamage) / originalTotalDamage) * 100 
      : 0;

    return {
      originalOrder: [...playerUnits],
      optimizedOrder,
      damageMatrix: { calculations: [], attackers: [], targets: [], calculationTimeMs: 0 },
      originalTotalDamage,
      optimizedTotalDamage,
      improvementPercentage,
      side: 'player',
      optimizationTimeMs: performance.now() - startTime
    };
  }

  /**
   * Optimize enemy attack order (simplified synchronous version)
   */
  private optimizeEnemyOrder(
    enemyUnits: EnemyUnit[],
    playerUnits: StackingGroup[],
    config: OptimizationConfig
  ): any {
    const startTime = performance.now();

    if (!config.optimizeEnemyOrder) {
      return this.createNoOptimizationResult(enemyUnits, 'enemy', startTime);
    }

    // Simple optimization: sort by strength
    const optimizedOrder = [...enemyUnits].sort((a, b) => b.strength - a.strength);

    const originalTotalDamage = enemyUnits.reduce((total, unit) => total + unit.strength, 0);
    const optimizedTotalDamage = optimizedOrder.reduce((total, unit) => total + unit.strength, 0);
    const improvementPercentage = 0; // Same total damage, just different order

    return {
      originalOrder: [...enemyUnits],
      optimizedOrder,
      damageMatrix: { calculations: [], attackers: [], targets: [], calculationTimeMs: 0 },
      originalTotalDamage,
      optimizedTotalDamage,
      improvementPercentage,
      side: 'enemy',
      optimizationTimeMs: performance.now() - startTime
    };
  }

  /**
   * Calculate simple damage for a player unit against all enemies
   */
  private calculateSimpleDamage(playerUnit: StackingGroup, enemyUnits: EnemyUnit[]): number {
    const baseDamage = playerUnit.count * playerUnit.unitStrength;
    
    // Apply simple modifiers based on unit types
    let totalDamage = 0;
    for (const enemy of enemyUnits) {
      let effectiveDamage = baseDamage;
      
      // Get player unit data to access attack modifiers
      const playerUnitData = this.unitLoader ? this.unitLoader.getUnitByName(playerUnit.unitName) : null;
      
      // Apply player unit attack modifiers (offensive bonuses)
      if (playerUnitData && playerUnitData.attack_modifiers) {
        for (const modifier of playerUnitData.attack_modifiers) {
          if (enemy.unit_types.some(type => type.toLowerCase() === modifier.target_type.toLowerCase())) {
            // Apply player's offensive modifier against this enemy type
            effectiveDamage += modifier.value * playerUnit.count;
          }
        }
      }
      
      totalDamage += Math.max(0, effectiveDamage);
    }
    
    return totalDamage;
  }

  /**
   * Calculate total simple damage for all units
   */
  private calculateTotalSimpleDamage(units: StackingGroup[], enemyUnits: EnemyUnit[]): number {
    return units.reduce((total, unit) => total + this.calculateSimpleDamage(unit, enemyUnits), 0);
  }

  /**
   * Create a no-optimization result
   */
  private createNoOptimizationResult(units: any[], side: string, startTime: number): any {
    return {
      originalOrder: [...units],
      optimizedOrder: [...units],
      damageMatrix: { calculations: [], attackers: [], targets: [], calculationTimeMs: 0 },
      originalTotalDamage: 0,
      optimizedTotalDamage: 0,
      improvementPercentage: 0,
      side,
      optimizationTimeMs: performance.now() - startTime
    };
  }

  /**
   * Create cache key for optimization
   */
  private createOptimizationCacheKey(
    playerUnits: StackingGroup[],
    enemyUnits: EnemyUnit[],
    config: OptimizationConfig
  ): string {
    const playerKey = playerUnits.map(u => `${u.unitName}:${u.count}:${u.unitStrength}`).join('|');
    const enemyKey = enemyUnits.map(u => `${u.name}:${u.strength}`).join('|');
    const configKey = `${config.optimizePlayerOrder}:${config.optimizeEnemyOrder}:${config.useUnitModifiers}`;
    return `${playerKey}::${enemyKey}::${configKey}`;
  }

  /**
   * Enhanced attack order calculation with optimization
   */
  calculateOptimizedAttackOrder(
    groups: StackingGroup[],
    enemyUnits?: EnemyUnit[],
    config: OptimizationConfig = DEFAULT_OPTIMIZATION_CONFIG
  ): StackingGroup[] {
    // If optimization is disabled or no enemy units provided, use standard calculation
    if (!config.optimizePlayerOrder || !enemyUnits || enemyUnits.length === 0) {
      return this.calculateAttackOrder(groups);
    }

    try {
      // Use simplified optimization
      const optimization = this.optimizePlayerOrder(groups, enemyUnits, config);
      return optimization.optimizedOrder;
    } catch (error) {
      console.warn('Attack order optimization failed, using standard calculation:', error);
      return this.calculateAttackOrder(groups);
    }
  }

  /**
   * Get optimization comparison between standard and optimized approaches
   */
  compareOptimizationBenefit(
    playerArmy: ArmyComposition,
    enemyUnits: EnemyUnit[],
    config: OptimizationConfig = DEFAULT_OPTIMIZATION_CONFIG
  ): {
    standardResult: BattleResult;
    optimizedResult: OptimizedBattleResult;
    improvement: {
      damageIncrease: number;
      damageIncreasePercentage: number;
      survivalIncrease: number;
      optimizationTimeMs: number;
    };
  } {
    // Run standard simulation
    const standardConfig: EnemyUnitBattleConfiguration = {
      playerArmy,
      enemyUnits,
      playerGoesFirst: true,
      useEnemyModifiers: true
    };
    const standardResult = this.simulateBattleWithEnemyUnits(standardConfig);

    // Run optimized simulation
    const optimizedConfig: OptimizedEnemyUnitBattleConfiguration = {
      ...standardConfig,
      useOptimization: true,
      optimizationConfig: config
    };
    const optimizedResult = this.simulateOptimizedBattleWithEnemyUnits(optimizedConfig);

    // Calculate improvement metrics
    const damageIncrease = optimizedResult.totalDamageDealtToEnemies - standardResult.totalDamageDealtToEnemies;
    const damageIncreasePercentage = standardResult.totalDamageDealtToEnemies > 0 
      ? (damageIncrease / standardResult.totalDamageDealtToEnemies) * 100 
      : 0;
    const survivalIncrease = optimizedResult.playerSurvivalTurns - standardResult.playerSurvivalTurns;
    const optimizationTimeMs = optimizedResult.optimizationResult?.combinedMetrics.totalOptimizationTimeMs || 0;

    return {
      standardResult,
      optimizedResult,
      improvement: {
        damageIncrease,
        damageIncreasePercentage,
        survivalIncrease,
        optimizationTimeMs
      }
    };
  }

  /**
   * Simulate both scenarios with enemy units, using enhanced damage calculation with player unit attack modifiers
   */
  simulateBothScenariosWithEnemyUnits(playerArmy: ArmyComposition, enemyUnits: EnemyUnit[], useEnemyModifiers: boolean = true): BattleAnalysis {
    // Use optimized battle simulation with player unit attack modifiers
    const config: OptimizedEnemyUnitBattleConfiguration = {
      playerArmy,
      enemyUnits,
      playerGoesFirst: true,
      useEnemyModifiers,
      useOptimization: true,
      optimizationConfig: DEFAULT_OPTIMIZATION_CONFIG
    };

    // Run best case scenario (player goes first)
    const bestCaseResult = this.simulateOptimizedBattleWithEnemyUnits({
      ...config,
      playerGoesFirst: true
    });

    // Run worst case scenario (enemy goes first)
    const worstCaseResult = this.simulateOptimizedBattleWithEnemyUnits({
      ...config,
      playerGoesFirst: false
    });

    // Calculate comparison metrics
    const damageDifference = bestCaseResult.totalDamageDealtToEnemies - worstCaseResult.totalDamageDealtToEnemies;
    const survivalDifference = bestCaseResult.playerSurvivalTurns - worstCaseResult.playerSurvivalTurns;
    const averageDamage = (bestCaseResult.totalDamageDealtToEnemies + worstCaseResult.totalDamageDealtToEnemies) / 2;
    const averageSurvival = (bestCaseResult.playerSurvivalTurns + worstCaseResult.playerSurvivalTurns) / 2;

    return {
      bestCase: bestCaseResult,
      worstCase: worstCaseResult,
      comparison: {
        damageDifference,
        survivalDifference,
        averageDamage,
        averageSurvival
      }
    };
  }

  /**
   * Clear optimization cache
   */
  clearOptimizationCache(): void {
    this.optimizationCache.clear();
    attackOrderOptimizerService.clearCache();
  }
}

/**
 * Singleton instance of the optimized battle simulation service
 */
export const optimizedBattleSimulationService = new OptimizedBattleSimulationService();