/**
 * Manual Battle Simulation Types
 * Types for user-configured battle scenarios where both player and enemy armies are manually set up
 */

import { Unit, StackingGroup } from './units';
import { BattleResult, BattleAnalysis, BattleConfiguration } from './battleSimulation';

/**
 * Configuration for a single army side in manual battle
 */
export interface ManualArmyConfiguration {
  /** Units in this army with their quantities */
  units: ManualArmyUnit[];
  /** Display name for this army */
  name: string;
  /** Side identifier */
  side: 'player' | 'enemy';
}

/**
 * A unit in a manually configured army
 */
export interface ManualArmyUnit {
  /** Reference to the unit definition */
  unit: Unit;
  /** Number of this unit type in the army */
  quantity: number;
  /** Whether this unit has infinite quantity (enemy side only) */
  isInfinite: boolean;
}

/**
 * Complete manual battle configuration
 */
export interface ManualBattleConfiguration {
  /** Player army configuration */
  playerArmy: ManualArmyConfiguration;
  /** Enemy army configuration */
  enemyArmy: ManualArmyConfiguration;
  /** Whether player gets first attack */
  playerGoesFirst: boolean;
  /** Battle name/description */
  battleName?: string;
}

/**
 * Extended battle result that includes manual configuration info
 */
export interface ManualBattleResult extends BattleResult {
  /** Original manual battle configuration */
  manualConfiguration: ManualBattleConfiguration;
  /** Enemy units that survived (for infinite units) */
  enemySurvivors?: ManualArmyUnit[];
}

/**
 * Manual battle analysis with both scenarios
 */
export interface ManualBattleAnalysis {
  /** Best case scenario result (player attacks first) */
  bestCase: ManualBattleResult;
  /** Worst case scenario result (enemy attacks first) */
  worstCase: ManualBattleResult;
  /** Comparison metrics between scenarios */
  comparison: {
    /** Difference in damage dealt between best and worst case */
    damageDifference: number;
    /** Difference in survival turns between best and worst case */
    survivalDifference: number;
    /** Average damage across both scenarios */
    averageDamage: number;
    /** Average survival turns across both scenarios */
    averageSurvival: number;
  };
  /** Original manual configuration */
  manualConfiguration: ManualBattleConfiguration;
}

/**
 * Validation result for manual army configuration
 */
export interface ManualArmyValidationResult {
  /** Whether the army configuration is valid */
  isValid: boolean;
  /** Validation error messages */
  errors: string[];
  /** Warning messages (non-blocking) */
  warnings: string[];
  /** Total unit count */
  totalUnits: number;
  /** Whether army has infinite units */
  hasInfiniteUnits: boolean;
}

/**
 * Unit selection state for dual army input
 */
export interface UnitSelectionState {
  /** Currently selected unit (if any) */
  selectedUnit: Unit | null;
  /** Current quantity input */
  quantity: number;
  /** Whether infinite is enabled (enemy side only) */
  isInfinite: boolean;
  /** Whether this selection is valid */
  isValid: boolean;
  /** Validation message */
  validationMessage?: string;
}

/**
 * Props for dual army input component
 */
export interface DualArmyInputProps {
  /** Available units to choose from for player army */
  availableUnits: Unit[];
  /** Available units to choose from for enemy army (defaults to availableUnits if not provided) */
  availableEnemyUnits?: Unit[];
  /** Current player army configuration */
  playerArmy: ManualArmyConfiguration;
  /** Current enemy army configuration */
  enemyArmy: ManualArmyConfiguration;
  /** Callback when player army changes */
  onPlayerArmyChange: (army: ManualArmyConfiguration) => void;
  /** Callback when enemy army changes */
  onEnemyArmyChange: (army: ManualArmyConfiguration) => void;
  /** Whether the form is disabled */
  disabled?: boolean;
}

/**
 * Utility functions for manual battle configuration
 */
export class ManualBattleUtils {
  /**
   * Convert manual army configuration to stacking groups for simulation
   */
  static convertToStackingGroups(army: ManualArmyConfiguration): StackingGroup[] {
    return army.units
      .filter(armyUnit => armyUnit.quantity > 0 || armyUnit.isInfinite)
      .map(armyUnit => ({
        unitName: armyUnit.unit.name,
        count: armyUnit.isInfinite ? 999999 : armyUnit.quantity, // Use large number for infinite
        totalHealth: armyUnit.isInfinite 
          ? armyUnit.unit.health * 999999 
          : armyUnit.unit.health * armyUnit.quantity,
        unitStrength: armyUnit.unit.strength
      }))
      .sort((a, b) => a.totalHealth - b.totalHealth); // Sort by total health (weakest first)
  }

  /**
   * Convert manual battle configuration to standard battle configuration
   */
  static convertToBattleConfiguration(
    manualConfig: ManualBattleConfiguration
  ): BattleConfiguration {
    const playerStackingGroups = this.convertToStackingGroups(manualConfig.playerArmy);
    
    // For enemy army, we need to count total enemy "groups" for the existing battle system
    const enemyGroups = manualConfig.enemyArmy.units
      .filter(unit => unit.quantity > 0 || unit.isInfinite)
      .reduce((total, unit) => total + (unit.isInfinite ? 100 : unit.quantity), 0); // Cap infinite at 100 groups
    
    return {
      playerArmy: {
        units: {}, // Legacy format - we use stackingOrder
        stackingOrder: playerStackingGroups,
        totalStrength: playerStackingGroups.reduce((sum, group) => sum + (group.count * group.unitStrength), 0),
        totalHealth: playerStackingGroups.reduce((sum, group) => sum + group.totalHealth, 0),
        totalLeadershipCost: 0, // Not used in manual battle
        totalDominanceCost: 0, // Not used in manual battle
        totalMercenaryCount: 0, // Not used in manual battle
        isValidStacking: true,
        efficiencyScore: 0 // Not used in manual battle
      },
      enemyGroupCount: Math.min(enemyGroups, 100), // Cap at 100 for performance
      playerGoesFirst: manualConfig.playerGoesFirst
    };
  }

  /**
   * Validate manual army configuration
   */
  static validateArmyConfiguration(army: ManualArmyConfiguration): ManualArmyValidationResult {
    const errors: string[] = [];
    const warnings: string[] = [];
    let totalUnits = 0;
    let hasInfiniteUnits = false;

    // Check if army has any units
    if (army.units.length === 0) {
      errors.push('Army must have at least one unit type');
    }

    // Validate each unit
    for (const armyUnit of army.units) {
      if (!armyUnit.unit) {
        errors.push('Invalid unit configuration');
        continue;
      }

      if (armyUnit.isInfinite) {
        if (army.side !== 'enemy') {
          errors.push(`Infinite units are only allowed for enemy armies`);
        } else {
          hasInfiniteUnits = true;
          totalUnits += 999999; // Count as large number for display
        }
      } else {
        if (armyUnit.quantity <= 0) {
          errors.push(`Unit "${armyUnit.unit.name}" must have a positive quantity`);
        } else if (armyUnit.quantity > 10000) {
          warnings.push(`Unit "${armyUnit.unit.name}" has a very large quantity (${armyUnit.quantity})`);
        }
        
        totalUnits += armyUnit.quantity;
      }
    }

    // Check for reasonable army size (unless infinite)
    if (!hasInfiniteUnits && totalUnits > 50000) {
      warnings.push(`Army size is very large (${totalUnits} total units). This may affect performance.`);
    }

    // Check for duplicate units
    const unitNames = army.units.map(u => u.unit.name);
    const duplicates = unitNames.filter((name, index) => unitNames.indexOf(name) !== index);
    if (duplicates.length > 0) {
      errors.push(`Duplicate units found: ${Array.from(new Set(duplicates)).join(', ')}`);
    }

    return {
      isValid: errors.length === 0,
      errors,
      warnings,
      totalUnits,
      hasInfiniteUnits
    };
  }

  /**
   * Create empty manual army configuration
   */
  static createEmptyArmy(side: 'player' | 'enemy', name: string): ManualArmyConfiguration {
    return {
      units: [],
      name,
      side
    };
  }

  /**
   * Add unit to manual army configuration
   */
  static addUnitToArmy(
    army: ManualArmyConfiguration, 
    unit: Unit, 
    quantity: number, 
    isInfinite: boolean = false
  ): ManualArmyConfiguration {
    // Check if unit already exists
    const existingIndex = army.units.findIndex(u => u.unit.name === unit.name);
    
    const newUnits = [...army.units];
    if (existingIndex >= 0) {
      // Update existing unit
      newUnits[existingIndex] = {
        unit,
        quantity: isInfinite ? 0 : quantity,
        isInfinite
      };
    } else {
      // Add new unit
      newUnits.push({
        unit,
        quantity: isInfinite ? 0 : quantity,
        isInfinite
      });
    }

    return {
      ...army,
      units: newUnits
    };
  }

  /**
   * Remove unit from manual army configuration
   */
  static removeUnitFromArmy(army: ManualArmyConfiguration, unitName: string): ManualArmyConfiguration {
    return {
      ...army,
      units: army.units.filter(u => u.unit.name !== unitName)
    };
  }

  /**
   * Get army summary for display
   */
  static getArmySummary(army: ManualArmyConfiguration): string {
    if (army.units.length === 0) {
      return 'No units configured';
    }

    const totalTypes = army.units.length;
    const infiniteCount = army.units.filter(u => u.isInfinite).length;
    const finiteTotal = army.units
      .filter(u => !u.isInfinite)
      .reduce((sum, u) => sum + u.quantity, 0);

    if (infiniteCount > 0) {
      return `${totalTypes} unit types (${infiniteCount} infinite, ${finiteTotal} finite)`;
    } else {
      return `${totalTypes} unit types, ${finiteTotal} total units`;
    }
  }
}

/**
 * Constants for manual battle simulation
 */
export const MANUAL_BATTLE_CONSTANTS = {
  /** Maximum finite units per army unit type */
  MAX_FINITE_UNITS: 10000,
  /** Maximum army unit types */
  MAX_ARMY_UNIT_TYPES: 50,
  /** Infinite unit placeholder value */
  INFINITE_UNIT_VALUE: 999999,
  /** Maximum enemy groups for simulation performance */
  MAX_ENEMY_GROUPS: 100
} as const;