/**
 * Unit Type Mapping Utilities
 * Provides access to unit types for StackingGroup objects during battle simulation
 */

import { Unit } from '../types/units';
import { StackingGroup } from '../types/units';

/**
 * Service for looking up unit types from StackingGroup unit names
 */
export class UnitTypeMappingService {
  private unitLoader: any = null; // Will be injected

  /**
   * Initialize the mapping service with unit loader
   */
  initialize(unitLoader: any): void {
    this.unitLoader = unitLoader;
  }

  /**
   * Get unit types for a StackingGroup by looking up the full Unit object
   */
  getUnitTypes(unitName: string): string[] {
    if (!this.unitLoader) {
      throw new Error('Unit loader not initialized');
    }

    const unit: Unit | undefined = this.unitLoader.getUnitByName(unitName);
    if (!unit) {
      console.warn(`Unit not found: ${unitName}`);
      return [];
    }

    return unit.unit_types || [];
  }

  /**
   * Get the full Unit object for a StackingGroup
   */
  getUnit(unitName: string): Unit | undefined {
    if (!this.unitLoader) {
      throw new Error('Unit loader not initialized');
    }

    return this.unitLoader.getUnitByName(unitName);
  }

  /**
   * Get the full Unit object with all data including attack modifiers
   */
  getFullUnitData(unitName: string): any {
    return this.getUnit(unitName);
  }

  /**
   * Check if a unit has a specific type
   */
  hasUnitType(unitName: string, unitType: string): boolean {
    const types = this.getUnitTypes(unitName);
    return types.some(type => type.toLowerCase() === unitType.toLowerCase());
  }

  /**
   * Get all available unit types from loaded units
   */
  getAllUnitTypes(): string[] {
    if (!this.unitLoader) {
      throw new Error('Unit loader not initialized');
    }

    return this.unitLoader.getUniqueUnitTypes();
  }

  /**
   * Validate that all units in stacking groups can be found
   */
  validateStackingGroups(stackingGroups: StackingGroup[]): {
    isValid: boolean;
    missingUnits: string[];
    warnings: string[];
  } {
    const missingUnits: string[] = [];
    const warnings: string[] = [];

    for (const group of stackingGroups) {
      const unit = this.getUnit(group.unitName);
      if (!unit) {
        missingUnits.push(group.unitName);
      } else if (!unit.unit_types || unit.unit_types.length === 0) {
        warnings.push(`Unit ${group.unitName} has no unit types defined`);
      }
    }

    return {
      isValid: missingUnits.length === 0,
      missingUnits,
      warnings
    };
  }
}

/**
 * Singleton instance of the unit type mapping service
 */
export const unitTypeMappingService = new UnitTypeMappingService();